/* NOTE: Pull in the external files -- WARN: must remain at top of this file */

@import './defaults.css';
@import './burger.css';
@import './below800px.css';
@import './above800px.css' screen and (min-width: 800px);
@import './forms.css';


/* NOTE: Kevin's standard defaults */
/* NOTE: This class is assoc. with the 'section' element and hence
determines style for all the text */
.section-background {
  /* display: none; */
  background-color: var(--clr-primary-200);
}

.bg-primary {
  color: var(--clr-neutral-100);
  background-color: var(--clr-primary-400);
}

.bg-dark {
  background-color: var(--clr-neutral-900);
}

/* NOTE: Kevin's approach - utility(?): */
.bg-primary h2,
.bg-dark h2 {
  color: var(--clr-primary-200);
}

a {
  color: #0f46ad9f;
  text-decoration: none;
  font-weight: normal;
}

/* NOTE: Format most the lists points as follows. There are some
overwrites e.g. in topLinks */
li {
  padding: 1rem;
  box-sizing: border-box;
  background-color: transparent;
  font-size: 1.2rem;
  line-height: 1.5;
}

/* NOTE: .active controlled in code */
li.active {
  color: #0017eb;
}

/* NOTE: li[a gap].active  doesn't work */
li.active a {
  color: #0017eb;
  text-decoration: none;
  font-weight: normal;
}

img.philImage {
  max-width: 100%;
  padding: 1rem;
}

header {
  flex-direction: row;
  position: fixed;
  z-index: 3;
  width: 100%;
}

nav ul {
  padding: 0;
  list-style: none;
  align-items: center;

  flex-wrap: wrap;
}

nav li {
  margin-bottom: 1em;
  margin-left: 1em;
}

nav a {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
  text-transform: uppercase;
  /* opacity: 0; */
  transition: opacity 150ms ease-in-out;
}

nav a:hover {
  color: var(--clr-primary-400);
}

.navLink {
  display: flex;
  justify-content: space-between;
  left: 0px;
  margin-top: 0px;
  top: 35px;
  /* Horizontally space out links */
}

nav img {
  /* display: none; */
}

.nav-toggle:checked ~ nav {
  display: block;
  transform: scale(1, 1);
}

/* NOTE: ~ nav - means look for a nav that's a sibling of nav-toggle  */
.nav-toggle:checked ~ nav a {
  opacity: 1;
  transition: opacity 250ms ease-in-out 250ms;
}

/* changed this from the tutorial video to
   allow it to gain focus, making it tabbable */
.nav-toggle {
  position: absolute !important;
  top: -9999px !important;
  left: -9999px !important;
}

.nav-toggle:focus ~ .nav-toggle-label {
  outline: 3px solid rgba(lightblue, 0.75);
}

/* NOTE: Create the hamburger menu on the span*/
.nav-toggle-label {
  /* display: none; */
  position: absolute;
  top: 0;
  left: 0;
  padding: 2em;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  border: 1rem white;
  /* background-color: var(--clr-neutral-900-with-opacity); */
  background-color: blue;
}

/* NOTE: ::before/after create a way to draw the hamburger */
.nav-toggle-label span,
.nav-toggle-label span::before,
.nav-toggle-label span::after {
  display: block;
  background: white;
  height: 120px;
  width: 120em;
  border-radius: 12px;
  position: relative;
}

.nav-toggle-label span::before,
.nav-toggle-label span::after {
  /* NOTE: If don't add content at all nothing will show */
  content: '';
  position: absolute;
}

.nav-toggle-label span::before {
  bottom: 17px;
}

.nav-toggle-label span::after {
  top: 17px;
}

.highlighted {
  background-color: var(--clr-primary-400-half-opacity);
}

